.slider {
    max-width: 960px;
    margin: 20px auto;
    position: relative;
}
.next {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: -18px;
    background-color: #fff;
    width: 35px;
    height: 35px;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
        width: 10px;
    }
}